"use client";
// Tabla individual de docentes por temporada
async function getTablaDocentes(temporada: number) {
    try {
        // Obtener todos los usuarios docentes desde la API
        const response = await fetch('/api/users');
        if (!response.ok) return {};

        const users = await response.json();
        const docentes = users.filter((u: any) => u.tipo === "Docente");

        // Para cada docente, obtener sus estadÃ­sticas de campeonato
        const resultado: Record<string, number> = {};
        for (const docente of docentes) {
            try {
                const statsResponse = await fetch(`/api/stats/championship?nick=${docente.nick}&temporada=t${temporada}&tipo=docentes`);
                if (statsResponse.ok) {
                    const stats = await statsResponse.json();
                    if (stats.likes) {
                        resultado[docente.nick] = stats.likes;
                    }
                }
            } catch (error) {
                console.error(`Error obteniendo stats para ${docente.nick}:`, error);
            }
        }

        // Ordenar y limitar a los 25 mejores
        const ordenados = Object.entries(resultado)
            .sort((a, b) => (b[1] as number) - (a[1] as number))
            .slice(0, 25);
        return Object.fromEntries(ordenados);
    } catch (error) {
        console.error('Error obteniendo tabla docentes:', error);
        return {};
    }
}
import React from "react";
import ChampionshipQuiz from "../../components/ChampionshipQuiz";
import TournamentQuiz from "../../components/TournamentQuiz";
import { BUTTON_STYLES } from "../../utils/styles";

export default function AprendeConPipo() {
    // LÃ³gica de temporada
    function getCurrentSeason() {
        const now = new Date();
        const year = now.getFullYear();
        // Temporada inicia el 1 de octubre y termina el 30 de septiembre del siguiente aÃ±o
        if (now.getMonth() + 1 < 10) {
            // Antes de octubre, temporada es del aÃ±o anterior
            return year - 1;
        }
        return year;
    }

    // Estado para temporada seleccionada
    const [temporadaSeleccionada, setTemporadaSeleccionada] = React.useState<number>(getCurrentSeason());

    // Estado para tabla de docentes
    const [tablaDocentes, setTablaDocentes] = React.useState<Record<string, number>>({});

    // Estado para mostrar modo competiciÃ³n
    const [modoCompeticion, setModoCompeticion] = React.useState(false);
    const [torneoIniciado, setTorneoIniciado] = React.useState(false);
    const [modoTorneoManual, setModoTorneoManual] = React.useState(false);
    const [isPremium, setIsPremium] = React.useState(false);
    // Estado para modo torneo premium
    const [modoTorneo, setModoTorneo] = React.useState(false);
    const [torneoActivo, setTorneoActivo] = React.useState<any>(null);
    // Detectar modo torneo por query param
    React.useEffect(() => {
        if (typeof window !== "undefined") {
            const params = new URLSearchParams(window.location.search);
            if (params.get("torneo") === "1") {
                setModoTorneoManual(true);
            }
        }
    }, []);
    // Estado para curso seleccionado en ChampionshipQuiz
    const [cursoCompeticion, setCursoCompeticion] = React.useState(1);
    // Estado para centro escolar (puedes adaptar segÃºn tu lÃ³gica de usuario)
    const [centroCompeticion, setCentroCompeticion] = React.useState("");
    // Ejemplo de cursos y asignaturas
    const cursos = ["1Âº Primaria", "2Âº Primaria", "3Âº Primaria", "4Âº Primaria", "5Âº Primaria", "6Âº Primaria"];
    const asignaturas = ["naturaleza", "matematicas", "lenguaje", "literatura", "ingles", "geografia", "historia", "general", "campeonato"];
    const displayAsignaturas = ["Naturaleza", "MatemÃ¡ticas", "Lenguaje", "Literatura", "InglÃ©s", "GeografÃ­a", "Historia", "General", "Campeonato"];

    // Cargar tabla de docentes cuando cambie la temporada
    React.useEffect(() => {
        const loadTablaDocentes = async () => {
            const tabla = await getTablaDocentes(temporadaSeleccionada);
            setTablaDocentes(tabla);
        };
        loadTablaDocentes();
    }, [temporadaSeleccionada]);

    // Actualizar temporada seleccionada desde el desplegable
    function handleTemporadaChange(e: React.ChangeEvent<HTMLSelectElement>) {
        setTemporadaSeleccionada(Number(e.target.value));
    }

    // Likes individuales por temporada (migrado a API)
    async function getLikesIndividual(nick: string, temporada: number): Promise<number> {
        try {
            const response = await fetch(`/api/stats/championship?nick=${nick}&temporada=t${temporada}&tipo=individual`);
            if (response.ok) {
                const stats = await response.json();
                return stats.likes || 0;
            }
        } catch (error) {
            console.error('Error obteniendo likes individuales:', error);
        }
        return 0;
    }

    // Likes de centro por temporada (migrado a API)
    async function getLikesCentro(centro: string, temporada: number): Promise<number> {
        try {
            const response = await fetch(`/api/stats/championship-table?temporada=t${temporada}&tipo=centro`);
            if (response.ok) {
                const tabla = await response.json();
                return tabla[centro] || 0;
            }
        } catch (error) {
            console.error('Error obteniendo likes de centro:', error);
        }
        return 0;
    }

    // Sumar likes (migrado a API)
    async function sumarLikes(nick: string, centro: string, cantidad: number): Promise<void> {
        try {
            await fetch('/api/stats/championship-update', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ nick, centro, temporada: `t${getCurrentSeason()}`, cantidad })
            });
        } catch (error) {
            console.error('Error sumando likes:', error);
        }
    }

    async function sumarLikesPerfil(nick: string, cantidad: number): Promise<void> {
        try {
            await fetch('/api/user/update-likes', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ likesDelta: cantidad })
            });
        } catch (error) {
            console.error('Error actualizando likes del perfil:', error);
        }
    }

    async function getTablaIndividual(temporada: number): Promise<Record<string, number>> {
        try {
            const response = await fetch(`/api/stats/championship-table?temporada=t${temporada}&tipo=individual`);
            if (response.ok) {
                return await response.json();
            }
        } catch (error) {
            console.error('Error obteniendo tabla individual:', error);
        }
        return {};
    }

    async function getTablaCentro(temporada: number): Promise<Record<string, number>> {
        try {
            const response = await fetch(`/api/stats/championship-table?temporada=t${temporada}&tipo=centro`);
            if (response.ok) {
                return await response.json();
            }
        } catch (error) {
            console.error('Error obteniendo tabla centro:', error);
        }
        return {};
    }

    // Estado para selecciÃ³n de curso y asignatura - SISTEMA ANTI-TRAMPA
    const [usuarioActual, setUsuarioActual] = React.useState<any>(null);
    const [cursoUsuario, setCursoUsuario] = React.useState<string>("1Âº Primaria");
    const [asignaturaSeleccionada, setAsignaturaSeleccionada] = React.useState<string>("matematicas");
    const [preguntaActual, setPreguntaActual] = React.useState<string>("");
    const [objetoPreguntaActual, setObjetoPreguntaActual] = React.useState<any>(null);
    const [respuestaCorrecta, setRespuestaCorrecta] = React.useState<string>("");
    const [respuestaUsuario, setRespuestaUsuario] = React.useState<string>("");
    const [feedback, setFeedback] = React.useState<string>("");
    // Estado para el temporizador: 5 minutos (300 segundos)
    const [timeLeft, setTimeLeft] = React.useState<number>(300); // 5 minutos por pregunta
    const [bloqueado, setBloqueado] = React.useState<boolean>(false);

    // âœ… SISTEMA ANTI-TRAMPA: Cargar usuario automÃ¡ticamente al iniciar
    React.useEffect(() => {
        if (typeof window === "undefined") return;

        // Get user from API
        fetch('/api/auth/me')
            .then(response => response.json())
            .then(data => {
                if (data.user) {
                    setUsuarioActual(data.user);

                    // Check premium from DB
                    fetch('/api/premium', {
                        method: 'POST',
                        headers: { 'Content-Type': 'application/json' },
                        body: JSON.stringify({ action: 'check', nick: data.user.nick })
                    })
                        .then(res => res.json())
                        .then(premiumData => {
                            setIsPremium(premiumData.premium);
                        })
                        .catch(() => { });

                    // âœ… SISTEMA DOCENTES: Detectar curso del usuario automÃ¡ticamente
                    let cursoDetectado = "1Âº Primaria";

                    // Si es docente, usar 6Âº curso por defecto (preguntas mÃ¡s difÃ­ciles)
                    const esDocente = data.user.esProfesor || data.user.tipo === "docente" || data.user.tipo === "Docente";

                    if (esDocente) {
                        cursoDetectado = "6Âº Primaria";
                    } else if (data.user.curso) {
                        // Para estudiantes, usar su curso real
                        if (typeof data.user.curso === "string") {
                            const match = data.user.curso.match(/(\d)/);
                            if (match) {
                                const numero = parseInt(match[1]);
                                cursoDetectado = `${numero}Âº Primaria`;
                            }
                        } else if (typeof data.user.curso === "number") {
                            cursoDetectado = `${data.user.curso}Âº Primaria`;
                        }
                    }
                    setCursoUsuario(cursoDetectado);
                }
            })
            .catch(error => {
                console.error("Error cargando datos del usuario:", error);
            });
    }, []);

    // Temporizador para modo clÃ¡sico
    React.useEffect(() => {
        if (!preguntaActual || bloqueado) return;
        if (timeLeft === 0) {
            setBloqueado(true);
            setFeedback("â° Tiempo agotado. No puedes responder esta pregunta. -2 likes");
            if (usuarioActual) {
                // Ejecutar en background sin bloquear
                sumarLikesPerfil(usuarioActual.nick, -2).catch(err => console.error('Error penalizando likes:', err));
            }
            return;
        }
        const timer = setTimeout(() => setTimeLeft(timeLeft - 1), 1000);
        return () => clearTimeout(timer);
    }, [timeLeft, preguntaActual, bloqueado]);

    // Cargar preguntas de todos los archivos segÃºn curso automÃ¡tico y materia
    const [preguntas, setPreguntas] = React.useState<any[]>([]);

    // Cargar preguntas desde la API
    React.useEffect(() => {
        const cargarPreguntas = async () => {
            let cursoNum = 1;
            if (cursoUsuario) {
                const match = cursoUsuario.match(/(\d+)/);
                if (match) cursoNum = Number(match[1]);
            }
            const materias = [
                "matematicas", "lenguaje", "literatura", "historia", "geografia", "naturaleza", "ingles", "general", "campeonato"
            ];
            let todas: any[] = [];
            for (const materia of materias) {
                try {
                    const response = await fetch(`/api/questions?curso=${cursoNum}primaria&asignatura=${materia}`);
                    if (response.ok) {
                        const preguntasMateria = await response.json();
                        todas = todas.concat(preguntasMateria);
                    }
                } catch {
                    // Si falla, lo ignora
                }
            }
            setPreguntas(todas);
        };
        cargarPreguntas();
    }, [cursoUsuario]);

    // Filtrar preguntas por asignatura
    const preguntasFiltradas = React.useMemo(() => {
        if (asignaturaSeleccionada === "General") return preguntas;
        // Filtrar solo preguntas con categoria igual a la asignatura seleccionada (ignorando mayÃºsculas y acentos)
        const filtradas = preguntas.filter((p: any) => p.categoria.toLowerCase().replace(/Ã¡/g, 'a').replace(/Ã©/g, 'e').replace(/Ã­/g, 'i').replace(/Ã³/g, 'o').replace(/Ãº/g, 'u') === asignaturaSeleccionada);
        return filtradas;
    }, [preguntas, asignaturaSeleccionada]);

    // Estado para preguntas ya usadas en la sesiÃ³n
    const [preguntasUsadas, setPreguntasUsadas] = React.useState<string[]>([]);

    function generarPregunta() {
        if (preguntasFiltradas.length === 0) {
            setPreguntaActual("");
            setObjetoPreguntaActual(null);
            setRespuestaCorrecta("");
            setFeedback("No hay preguntas para esta asignatura.");
            setBloqueado(false);
            setTimeLeft(300);
            setPreguntasUsadas([]);
            return;
        }
        // Filtrar preguntas no usadas
        const restantes = preguntasFiltradas.filter((p: any) => !preguntasUsadas.includes(p.pregunta));
        if (restantes.length === 0) {
            setPreguntaActual("");
            setObjetoPreguntaActual(null);
            setRespuestaCorrecta("");
            setFeedback("Â¡Has respondido todas las preguntas disponibles!");
            setBloqueado(true);
            setTimeLeft(300);
            setPreguntasUsadas([]);
            return;
        }
        const random = Math.floor(Math.random() * restantes.length);
        const preguntaSeleccionada = restantes[random];
        setPreguntaActual(preguntaSeleccionada.pregunta);
        setObjetoPreguntaActual(preguntaSeleccionada);  // Guardar objeto completo
        setRespuestaCorrecta(preguntaSeleccionada.respuesta);
        setRespuestaUsuario("");
        setFeedback("");
        setBloqueado(false);
        setTimeLeft(300);
        setPreguntasUsadas([...preguntasUsadas, preguntaSeleccionada.pregunta]);
    }

    async function comprobarRespuesta() {
        if (bloqueado) return;
        let likesDelta = 0;

        // âœ… SISTEMA DOCENTES: Detectar si es docente para ajustar puntuaciÃ³n
        const esDocente = usuarioActual?.esProfesor || usuarioActual?.tipo === "docente" || usuarioActual?.tipo === "Docente";
        const multiplicadorDocente = esDocente ? 0.5 : 1; // Los docentes reciben la mitad de puntos

        function normalizar(str: string) {
            return str.toLowerCase().normalize("NFD").replace(/\p{Diacritic}/gu, "").replace(/\s+/g, "").trim();
        }
        const esCorrecta = normalizar(respuestaUsuario) === normalizar(respuestaCorrecta);
        setBloqueado(true);
        if (esCorrecta) {
            if (timeLeft > 120) {
                const puntosBase = 1 * multiplicadorDocente;
                setFeedback(`Â¡Correcto! ğŸ‰ +${puntosBase} like${puntosBase !== 1 ? 's' : ''}${esDocente ? ' (Docente: 50% puntos)' : ''}`);
                likesDelta = puntosBase;
            } else {
                const puntosBase = 0.5 * multiplicadorDocente;
                setFeedback(`Â¡Correcto! ğŸ‰ +${puntosBase} likes${esDocente ? ' (Docente: 50% puntos)' : ''}`);
                likesDelta = puntosBase;
            }
            // Update DB stats
            if (usuarioActual) {
                fetch('/api/user/increment-stat', {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify({ stat: 'respuestasAcertadas', amount: 1 })
                }).catch(err => console.error('Error updating DB stats:', err));

                // âœ… NUEVO: Guardar respuestas acertadas por asignatura especÃ­fica
                if (objetoPreguntaActual && objetoPreguntaActual.categoria) {
                    // For now, we'll just increment general stats. Individual subject stats can be added later if needed.
                }
            }
        } else {
            if (timeLeft > 120) {
                const penalizacionBase = -0.5 * multiplicadorDocente;
                setFeedback(`Incorrecto. La respuesta era: ${respuestaCorrecta} ${penalizacionBase} likes${esDocente ? ' (Docente: 50% puntos)' : ''}`);
                likesDelta = penalizacionBase;
            } else {
                const penalizacionBase = -1 * multiplicadorDocente;
                setFeedback(`Incorrecto. La respuesta era: ${respuestaCorrecta} ${penalizacionBase} like${Math.abs(penalizacionBase) !== 1 ? 's' : ''}${esDocente ? ' (Docente: 50% puntos)' : ''}`);
                likesDelta = penalizacionBase;
            }
            // Update DB for failed questions
            if (usuarioActual) {
                fetch('/api/user/increment-stat', {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify({ stat: 'preguntasFalladas', amount: 1 })
                }).catch(err => console.error('Error updating DB stats falladas:', err));
            }
        }
        if (typeof window !== "undefined") {
            if (usuarioActual) {
                await sumarLikesPerfil(usuarioActual.nick, likesDelta);
            }
        }
        // Emitir evento para refrescar perfil
        window.dispatchEvent(new Event('profileUpdate'));
    }

    if (modoTorneoManual) {
        // Solo mostrar el torneo, ocultar todo lo demÃ¡s
        const handleTournamentComplete = async (aciertos: number, puntuacionTotal: number) => {
            try {
                const params = new URLSearchParams(window.location.search);
                const torneoId = params.get("torneoId") || "torneo-mensual-" + (cursoUsuario ? cursoUsuario[0] : "1") + "primaria";
                const res = await fetch('/api/premium/torneos');
                let torneoData = await res.json();
                let torneos = torneoData.torneos || [];
                if (typeof torneos === 'string') {
                    try {
                        torneos = JSON.parse(torneos);
                    } catch {
                        torneos = [];
                    }
                }
                torneos = torneos.map((t: any) => {
                    if (t.id === torneoId) {
                        // Evitar duplicados en resultados
                        let resultados = Array.isArray(t.resultados) ? [...t.resultados] : [];
                        if (!resultados.some((r: any) => r.nick === usuarioActual?.nick)) {
                            resultados.push({ nick: usuarioActual?.nick, aciertos, puntuacion: puntuacionTotal });
                        }
                        resultados.sort((a: any, b: any) => b.puntuacion - a.puntuacion);
                        // Evitar duplicados en participantes
                        let participantes = Array.isArray(t.participantes) ? [...t.participantes] : [];
                        if (!participantes.includes(usuarioActual?.nick)) {
                            participantes.push(usuarioActual?.nick);
                        }
                        // El estado global del torneo NO se marca como 'finalizado', solo el resultado del usuario
                        return { ...t, resultados, participantes };
                    }
                    return t;
                });
                await fetch('/api/premium/torneos', {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify({ torneos: JSON.stringify(torneos) })
                });
                // Actualizar estadÃ­sticas globales del usuario
                const statsRes = await fetch('/api/premium/competiciones');
                let stats = await statsRes.json();
                let participaciones = (stats.participaciones || 0) + 1;
                let puntuacionTotalGlobal = (stats.puntuacionTotal || 0) + puntuacionTotal;
                let victorias = stats.victorias || 0;
                if (torneos.find(t => t.id === torneoId)?.resultados?.[0]?.nick === usuarioActual?.nick) {
                    victorias += 1;
                }
                await fetch('/api/premium/competiciones', {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify({ victorias, participaciones, puntuacionTotal: puntuacionTotalGlobal })
                });
                setTimeout(() => {
                    window.location.href = '/torneos-premium';
                }, 500);
            } catch (err) {
                window.location.href = '/torneos-premium';
            }
        };
        return (
            <div className="min-h-screen bg-green-100 flex flex-col items-center justify-center">
                <TournamentQuiz userGrade={parseInt(cursoUsuario) || 1} onTournamentComplete={handleTournamentComplete} />
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-green-100 p-8 flex flex-col items-center">
            <h1 className="text-2xl font-bold mb-4 text-center">Aprende con Pipo</h1>
            <div className="flex items-stretch mx-auto" style={{ transform: 'translateX(-80px)' }}>
                <img src="/trofeo16.jpg" alt="Trofeo 16" className="h-40 object-contain mr-4" />
                <div className="max-w-2xl w-full bg-white rounded-lg shadow-md p-8 flex flex-col gap-6 mx-auto">
                    {!modoCompeticion ? (
                        <>
                            {/* âœ… SISTEMA ANTI-TRAMPA: InformaciÃ³n del usuario detectada automÃ¡ticamente */}
                            <div className="bg-gradient-to-r from-blue-50 to-green-50 p-4 rounded-lg border-2 border-blue-200 mb-4">
                                <h3 className="text-lg font-bold text-blue-800 mb-2">ğŸ›¡ï¸ Sistema Anti-Trampa Activo</h3>
                                <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
                                    <div>
                                        <span className="font-semibold text-blue-700">ğŸ‘¤ Usuario:</span>
                                        <span className="ml-2 text-gray-800">{usuarioActual?.nick || "No detectado"}</span>
                                    </div>
                                    <div>
                                        <span className="font-semibold text-blue-700">ğŸ“ Curso detectado:</span>
                                        <span className="ml-2 text-gray-800 font-semibold">{cursoUsuario}</span>
                                    </div>
                                    <div>
                                        <span className="font-semibold text-blue-700">ğŸ« Centro:</span>
                                        <span className="ml-2 text-gray-800">{usuarioActual?.centro || "No asignado"}</span>
                                    </div>
                                    <div>
                                        <span className="font-semibold text-blue-700">ğŸ‘¥ Tipo:</span>
                                        <span className="ml-2 text-gray-800">
                                            {(usuarioActual?.esProfesor || usuarioActual?.tipo === "docente" || usuarioActual?.tipo === "Docente") ? "Docente" : "Estudiante"}
                                        </span>
                                    </div>
                                </div>
                                <div className="mt-3 p-3 bg-yellow-50 border border-yellow-200 rounded">
                                    <p className="text-xs text-yellow-800">
                                        ğŸ”’ <strong>Solo podrÃ¡s responder preguntas de tu nivel</strong> para garantizar puntuaciones justas.
                                    </p>
                                    {(usuarioActual?.esProfesor || usuarioActual?.tipo === "docente" || usuarioActual?.tipo === "Docente") && (
                                        <p className="text-xs text-orange-800 mt-2">
                                            ğŸ‘©â€ğŸ« <strong>Modo Docente:</strong> Preguntas de 6Âº curso y puntuaciÃ³n reducida al 50% para equilibrar la dificultad.
                                        </p>
                                    )}
                                </div>
                            </div>

                            <div className="flex flex-col md:flex-row gap-4 items-center justify-center">
                                <label className="font-semibold">Asignatura:</label>
                                <select className="border rounded px-2 py-1" value={asignaturaSeleccionada} onChange={e => setAsignaturaSeleccionada(e.target.value)}>
                                    {asignaturas.map((a, i) => <option key={a} value={a}>{displayAsignaturas[i]}</option>)}
                                </select>
                                <div className="text-sm text-gray-600">
                                    (Curso fijo: <strong>{cursoUsuario}</strong>)
                                </div>
                            </div>
                            <button className={BUTTON_STYLES.primary + " mt-4"} onClick={generarPregunta}>
                                Generar pregunta
                            </button>
                            <button className={BUTTON_STYLES.orange + " mt-4 self-center"} onClick={() => {
                                // Obtener curso y centro escolar del usuario desde API
                                let cursoUsuario = 1;
                                let centroUsuario = "";
                                if (usuarioActual) {
                                    // El curso puede estar como nÃºmero (1-6) o como texto ("6Âº", "5Âº", ...)
                                    let cursoNum = 1;
                                    if (usuarioActual.curso) {
                                        if (typeof usuarioActual.curso === "string") {
                                            const match = usuarioActual.curso.match(/(\d)/);
                                            if (match) cursoNum = Number(match[1]);
                                        } else if (typeof usuarioActual.curso === "number") {
                                            cursoNum = usuarioActual.curso;
                                        }
                                    }
                                    if (!isNaN(cursoNum) && cursoNum >= 1 && cursoNum <= 6) {
                                        cursoUsuario = cursoNum;
                                    }
                                    // Centro escolar
                                    if (usuarioActual.centro) {
                                        centroUsuario = usuarioActual.centro;
                                    }
                                }
                                setModoCompeticion(true);
                                setCursoCompeticion(cursoUsuario);
                                setCentroCompeticion(centroUsuario);
                            }}>
                                Modo competiciÃ³n
                            </button>
                            {isPremium && (
                                <button className={BUTTON_STYLES.purple + " mt-4 self-center"} onClick={() => {
                                    setModoTorneoManual(true);
                                }}>
                                    Modo torneo
                                </button>
                            )}
                            {preguntaActual && (
                                <div className="mt-4">
                                    <div className="font-semibold mb-2">{preguntaActual}</div>
                                    <div className="font-bold text-lg mb-2">â° Tiempo: {Math.floor(timeLeft / 60)}:{(timeLeft % 60).toString().padStart(2, '0')} min</div>
                                    <input
                                        type="text"
                                        className="border rounded px-2 py-1 w-full mb-2"
                                        value={respuestaUsuario}
                                        onChange={e => setRespuestaUsuario(e.target.value)}
                                        placeholder="Escribe tu respuesta aquÃ­"
                                        disabled={bloqueado}
                                    />
                                    <button className={BUTTON_STYLES.success} onClick={comprobarRespuesta} disabled={bloqueado}>
                                        Comprobar respuesta
                                    </button>
                                    {feedback && <div className="mt-2 font-bold">{feedback}</div>}
                                </div>
                            )}
                            {feedback && !preguntaActual && <div className="mt-2 font-bold text-red-600">{feedback}</div>}

                            {/* Tabla Docentes */}
                            <div className="mt-8">
                                <h2 className="text-xl font-bold mb-2">Ranking Docentes</h2>
                                <table className="w-full border mb-4" style={{ backgroundColor: '#fffbe6' }}>
                                    <thead>
                                        <tr style={{ backgroundColor: '#fff9c4' }}>
                                            <th className="p-2">Docente</th>
                                            <th className="p-2">Likes</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {Object.entries(tablaDocentes).map(([nick, likes]) => (
                                            <tr key={nick}>
                                                <td className="border p-2">{nick}</td>
                                                <td className="border p-2">{likes}</td>
                                            </tr>
                                        ))}
                                    </tbody>
                                </table>
                            </div>
                        </>
                    ) : (
                        <div className="mt-4">
                            <ChampionshipQuiz userGrade={cursoCompeticion} userSchool={centroCompeticion} />
                            <button className={BUTTON_STYLES.secondary + " mt-4"} onClick={() => setModoCompeticion(false)}>
                                Salir de competiciÃ³n
                            </button>
                        </div>
                    )}
                </div>
                <img src="/logo-pipo.jpg" alt="Logo Pipo" className="h-40 object-contain ml-4" />
            </div>
        </div>
    );

    // ...existing code...
}
